import {ComponentProps} from 'react';
import {AsProp} from '~/shared/types';
import clsx from 'clsx';
import {classVariants} from '~/utils/class-variants';
import WhiteLeftLogo from '/src/assets/logos/white/left.svg';
import WhiteBadgeLogo from '/src/assets/logos/white/badge.svg';
import WhiteFullLogo from '/src/assets/logos/white/full.svg';
import BlackLeftLogo from '/src/assets/logos/black/left.svg';
import BlackBadgeLogo from '/src/assets/logos/black/badge.svg';
import BlackFullLogo from '/src/assets/logos/black/full.svg';
interface Props extends ComponentProps<'img'> {
  as?: AsProp;
  className?: string;
  alt?: string;
  theme?: 'black' | 'white';
  variant: 'full' | 'badge' | 'left';
}

export function Logo({
  as: Component = 'img',
  className = '',
  alt = 'Culture Kings',
  theme = 'black',
  variant = 'left',
  ...props
}: Props) {
  return (
    <Component
      src={classVariants(theme, {
        white: classVariants(variant, {
          full: BlackFullLogo,
          badge: BlackBadgeLogo,
          left: BlackLeftLogo,
        }),
        black: classVariants(variant, {
          full: WhiteFullLogo,
          badge: WhiteBadgeLogo,
          left: WhiteLeftLogo,
        }),
      })}
      className={clsx(
        'h-10',
        classVariants(variant, {
          full: 'auto',
          badge: 'w-9',
          left: 'auto',
        }),
        className,
      )}
      alt={alt}
      {...props}
    />
  );
}
